import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Image from "../components/image"
import Grid from "../components/grid"

import styled from "styled-components"

import { Link, graphql } from "gatsby"

// const products = [...Array(20)].map((item, index) => ({ img: "", index }))

const ProductsContainerItemDescription = styled.p`
  font-style: italic;
  hyphens: auto;
`
const ProductPageDescription = styled.p`
  font-style: italic;
`

const ProductItem = styled.div`
  padding: 2em 1em;
  transition: background-color 0.4s;
  :hover {
    background-color: lightgray;

    img {
      transform: scale(1.2);
    }
  }

  a {
    color: gray;
  }
`

const ProductsPage = ({ data }) => {
  console.log("PRODUCT INDEX, data:", data)

  const products = data.allProduct.edges

  return (
    <Layout>
      <SEO title="Products" />
      <h1>PRODUCTS</h1>
      <ProductPageDescription>
        Lorem ipsum dolor amet activated charcoal forage gentrify paleo mustache
        cray vinyl. Williamsburg cred green juice seitan vexillologist, whatever
        try-hard pickled keffiyeh shabby chic cold-pressed. Yr poutine flannel,
        90's readymade before they sold out small batch etsy. Ugh narwhal
        mustache, cardigan meditation keffiyeh master cleanse try-hard irony
        cloud bread quinoa.
      </ProductPageDescription>
      <div>
        <Grid
          items={products}
          render={({ item }) => (
            <ProductItem>
              <Link to={`products/${item.node.productId}`}>
                <h3>{item.node.name}</h3>
                <Image
                  name={item.node.imageId}
                  imgStyle={{ transition: "transform 0.4s ease" }}
                />
                <ProductsContainerItemDescription>
                  {item.node.description}
                </ProductsContainerItemDescription>
              </Link>
            </ProductItem>
          )}
        ></Grid>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    allProduct(sort: { fields: name, order: ASC }) {
      edges {
        node {
          name
          productId
          description
          imageId
        }
      }
    }
  }
`

export default ProductsPage
